.Save_Wish_Container {
    width: 340px;
    height: 480px;
    background: #fff;
    margin: 0 auto;
    position: relative;
    overflow: hidden;
    border: 2px solid #ccc;
    padding: 20px 5px 100px 5px;
    border-radius: 20px;
    -webkit-box-shadow: 0px 5px 5px 0px #ccc;
            box-shadow: 0px 5px 5px 0px #ccc;
}

#Save_Wish_speaker {
    width: 50px;
    height: 5px;
    border-radius: 15px;
    background: #f2f2f2;
    margin: 0px auto 15px;
    -webkit-box-shadow: inset 0px 0px 10px 0px #bdbdbd, inset 0px 5px 0px 0px #bdbdbd;
            box-shadow: inset 0px 0px 10px 0px #bdbdbd, inset 0px 5px 0px 0px #bdbdbd;
    border: 1px solid #c6c6c6;
}


#Save_Wish_controlder {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background: #f2f2f2;
    margin: 15px auto;
    -webkit-box-shadow: inset 0px 0px 10px 0px #bdbdbd;
            box-shadow: inset 0px 0px 10px 0px #bdbdbd;
    border: 1px solid #c6c6c6;
}


.Save_Wish_AppContainer {
    height: 100%;
    background-color: rgb(64, 72, 84);
    border: none;

    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: auto;

}


