.flip-card {
    background-color: transparent;
    width: 100%;
    height: 230px;
    -webkit-perspective: 1000px;
            perspective: 1000px;
  }
  
  .flip-card-inner {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    -webkit-transition: -webkit-transform 0.6s;
    transition: -webkit-transform 0.6s;
    -o-transition: transform 0.6s;
    transition: transform 0.6s;
    transition: transform 0.6s, -webkit-transform 0.6s;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
            box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  }
  
  .flip-card:hover .flip-card-inner {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
  }
  
  .flip-card-front, .flip-card-back {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
  }
  
  .flip-card-front {
    background-color: #244f6b9a;
    color: black;
    z-index: 2;
  }
  
  .flip-card-back {
    background-color: #244f6b9a;
    color: white;
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
    z-index: 1;
    overflow: scroll;
  }

  .btn_flip_open {
    background-color: rgb(84, 124, 73);
    border: none;
    color: rgb(255, 255, 255);
    padding: 10px 20px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    margin: 4px 2px;
    cursor: pointer;
    border-radius: 16px;
}

.btn_flip_open:hover {
    background-color: #96ad8b;
    color: rgb(88, 88, 88);
    font-weight: bold;
}


.btn_flip_delete {
  background-color: rgb(197, 55, 55);
  border: none;
  color: rgb(255, 255, 255);
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 16px;
}

.btn_flip_delete:hover {
  background-color: #b65d5d;
  color: rgb(88, 88, 88);
  font-weight: bold;
}


